<template>
	<a-modal v-model="visible" title="编辑新管理员" on-ok="handleOk" :afterClose="handleCancel" width="800px" :dialog-style="{ top: '100px' }">
		<template slot="footer">
			<a-button key="back" @click="handleCancel">
				取消
			</a-button>
			<a-button key="submit" type="primary" :loading="buttonloading" @click="handleOk">
				提交
			</a-button>
		</template>
		<div>
			<a-form :form="form" :label-col="labelCol" :wrapper-col="wrapperCol" hide-required-mark>
				<div style="margin-top: -10px; display: flex; justify-content: space-around;">
					<div>
						<a-form-item label="用户名">
							<a-input style="width: 300px;" v-decorator="['name', {rules: [
								{ required: true, message: '用户名不能为空' }],
								},
							]"
							 placeholder="请输入用户名" />
						</a-form-item>
						<a-form-item label="邮箱">
							<a-input style="width: 300px;" v-decorator="['email']" placeholder="请输入电子邮箱"/>
						</a-form-item>
						<a-form-item label="权限">
							<a-checkbox-group style="margin: 5px 0 0 5px; width: 300px;"  v-decorator="['access', {rules: [
								{ required: true, message: '权限不能为空' }],
								},
							]"
							 placeholder="请选择权限" >
								<a-checkbox :value="a.id" :name="a.menuKey" v-for="a in allAccessList" style="vertical-align:middle;">
									{{a.menuKey}}
								</a-checkbox>
							</a-checkbox-group>
						</a-form-item>
					</div>
					<a-form-item>
						<a-upload name="file" list-type="picture-card" class="poster-uploader" :show-upload-list="false" :action="this.$store.state.requestPath + 'uploadHeadPic'"
						 :before-upload="beforeUpload" @change="handleChange">
							<img v-if="imageUrl" :src="imageUrl" style="width: 200px; height: 300px;" />
							<div v-else>
								<a-icon :type="picloading ? 'loading' : 'plus'" />
								<div class="ant-upload-text">
									上传
								</div>
							</div>
						</a-upload>
					</a-form-item>
				</div>
			</a-form>
		</div>
	</a-modal>
</template>
<script>
	import {
		mapMutations,
		mapState
	} from 'vuex'

	function getBase64(img, callback) {
		const reader = new FileReader();
		reader.addEventListener('load', () => callback(reader.result));
		reader.readAsDataURL(img);
	};
	export default {
		computed: {
			...mapState(['userInfo', 'requestPath'])
		},
		props: ['visible', 'allAccessList'],
		data() {
			return {
				form: this.$form.createForm(this),
				labelCol: {
					span: 4
				},
				wrapperCol: {
					span: 14
				},
				imageUrl: '',
				admin: {
					"name": null,
					"email": null,
					"headPic": "default.jpg",
					"access": null,
				},
				picloading: false,
				buttonloading: false,
			};
		},
		methods: {
			async handleOk(e) {
				this.form.validateFields(async (err, values) => {
					if (!err) {
						this.admin.name = values.name;
						this.admin.email = values.email;
						this.admin.access = values.access;
						console.log(this.admin);
						let {
							data
						} = await this.axios.post(this.requestPath + 'addAdmin', this.admin);
						if (data.code == 0) {
							this.$message.success('添加成功');
							this.$emit('updateDataSource');				
						} else {
							this.$message.error('添加失败');
						}
					}
				});
				
				this.buttonloading = true;
				setTimeout(() => {
					this.$emit('updatePopWinState', false);
					this.buttonloading = false;
				}, 2000);
			},
			handleCancel(e) {
				this.imageUrl = null;
				this.$emit('updatePopWinState', false);
			},
			handleChange(info) {
				if (info.file.status === 'uploading') {
					this.picloading = true;
					return;
				}
				if (info.file.status === 'done') {
					// 从前端获取上传的 img-url
					getBase64(info.file.originFileObj, imageUrl => {
						this.imageUrl = imageUrl;
						this.picloading = false;
					});
					this.user.headPic = info.file.response.data.src;
				}
			},
			beforeUpload(file) {
				const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
				if (!isJpgOrPng) {
					this.$message.error('You can only upload JPG file!');
				}
				const isLt2M = file.size / 1024 / 1024 < 2;
				if (!isLt2M) {
					this.$message.error('Image must smaller than 2MB!');
				}
				return isJpgOrPng && isLt2M;
			},
		},
	}
</script>

<style lang='scss'>
	.poster-uploader>.ant-upload {
		width: 200px;
		height: 240px;
	}

	.ant-upload-select-picture-card i {
		font-size: 16;
		color: #999;
	}

	.ant-upload-select-picture-card .ant-upload-text {
		margin-top: 8px;
		color: #666;
	}
</style>
