<template>
	<div>
		<div>
			<a-button type="primary" icon="plus-circle" @click="showModal">
				添加
			</a-button>
			<AdminAddPage :visible="visible" :allAccessList="allAccessList"  @updateDataSource="updateDataSource" @updatePopWinState="updatePopWinState"></AdminAddPage>
			<div style="display: flex; justify-content: flex-start;">
				<div>
					搜关键字：
					<a-input style="width:250px; margin: 20px 0 ;" v-model="params.key"
					 placeholder="用户名/电子邮箱"/>
				</div>
				<div style="margin: 20px 0 0 25px">
					权限：
					<a-select placeholder="选择权限" v-model="params.access" style="width: 150px;" :allowClear="true">
						<a-select-option :value=access.id v-for="access in allAccessList">{{access.menuKey}}</a-select-option>
					</a-select>
				</div>
				<a-button type="primary" icon="search" style="margin: 20px 0 0 50px" @click="search">
					搜索
				</a-button>
			</div>

			<div v-show="!collapsed">
				<div>
					创建时间：
					<a-range-picker @change="onChange" style="width:250px; " :locale="locale"/>
				</div>
			</div>
			<a-divider style="color: #999999; margin-top: -2px;">
				<a-icon @click="collapsed=!collapsed" :type="collapsed?'down-circle':'up-circle'" />
				{{collapsed?"展开":"收起"}}更多选项
			</a-divider>
		</div>

		<a-table :columns="columns" :data-source="dataSource" :loading="dataloading" :pagination=" {pageSize:pageSize}" :scroll="{ y: 280 }">
			<b slot="name" slot-scope="text">{{ text }}</b>
			<span slot="access" slot-scope="text, record" v-if="record.accessList">
				<a-tag v-for="access in record.accessList" :color="access.tagColor">
					{{ access.menuKey }}
				</a-tag>
			</span>
			<span slot="action" slot-scope="text, record">
				<div style="display: flex; justify-content: center;">
					<a-popconfirm title="是否确定删除？" ok-text="是" cancel-text="否" @confirm="confirmDelete(record.uid)"
					 @cancel="cancel">
						<a>删除</a>
					</a-popconfirm>
					<a-divider type="vertical" />
					<router-view :record="record" :allAccessList="allAccessList" @updateDataSource="updateDataSource"></router-view>
					<a-divider type="vertical" />
					<a-popconfirm title="是否确定重置密码？" ok-text="是" cancel-text="否" @confirm="confirmReset(record.uid)"
					 @cancel="cancel">
						<a>重置密码</a>
					</a-popconfirm>
				</div>
			</span>
		</a-table>
	</div>

</template>

<script>
	import AdminAddPage from './AdminAddPage.vue'
	import locale from 'ant-design-vue/es/date-picker/locale/zh_CN';
	import {
		mapMutations,
		mapState
	} from "vuex";
	const columns = [
		{
			title: '用户名',
			dataIndex: 'name',
			scopedSlots: {
				customRender: 'name'
			},
		},
		{
			title: '电子邮箱',
			dataIndex: 'email',
		},
		{
			title: '创建时间',
			dataIndex: 'createdTime',
		},
		{
			title: '权限',
			dataIndex: 'access',
			scopedSlots: {
				customRender: 'access'
			},
		},
		{
			title: '操作',
			dataIndex: 'action',
			scopedSlots: {
				customRender: 'action'
			},
		},
	];
	columns.forEach((r) => {
		r.align = 'center'
	});

	export default {
		components: {
			AdminAddPage
		},
		data() {
			return {	
				locale,
				params : {
					key : null,
					access: null,
					startTime: null,
					endTime: null,
				},
				dataSource: null,
				page: 1,
				pageSize: 10,
				dataloading: true,
				visible: false,
				columns,
				collapsed: true,
				allAccessList: null,
			};
		},
		computed: {
			...mapState(['requestPath', 'userInfo'])
		},
		
		async created() {
			this.getAccessList();
			this.getAllAdmin();
		},
		methods: {
			onChange(date, dateString) {
				this.params.startTime = dateString[0];
				this.params.endTime = dateString[1];
			},
			async getAccessList(){
				if (this.$store.state.userInfo) {
					let {
						data
					} = await this.$get(this.$store.state.requestPath + 'allAccess');
					console.log(data)
					this.allAccessList = data.data;
				} else {
					console.log("userInfo not found");
				}
			},
			async getAllAdmin(){
				this.dataloading = true;
				if (this.$store.state.userInfo) {
					let {
						data
					} = await this.$get(this.$store.state.requestPath + 'allAdmin');
					console.log(data);
					this.dataSource = data.data;
					this.dataloading = false;
				} else {
					console.log("userInfo not found");
				}
			},
			async search(){
				this.dataloading = true;
				let {
					data
				} = await this.axios.post(this.requestPath + 'searchAdmin', this.params);
				this.dataSource = data.data;
				this.dataloading = false;
			},
			async confirmDelete(e) {
				console.log('del_uid:' + e);	
				let {
					data
				} = await this.$get(this.$store.state.requestPath + 'delAdmin/' + e);
				if (data.code == 0){
					this.$message.success('删除成功');
					const dataSource = [...this.dataSource];
					this.dataSource = dataSource.filter(item => item.uid !== e);
				}else{
					this.$message.error('删除失败');
				}	
			},
			async confirmReset(e) {
				console.log('reset_uid:' + e);	
				let {
					data
				} = await this.$get(this.$store.state.requestPath + 'resetAdminPwd/' + e);
				if (data.code == 0){
					this.$message.success('重置密码成功');
				}else{
					this.$message.error('重置密码失败');
				}	
			},
			
			cancel(e) {
				console.log(e);
			},
			showModal() {
				this.visible = true;
			},
			updatePopWinState(state){
				this.visible = state;
			},
			async updateDataSource(){
				this.getAllAdmin();
			},
		},
	};
</script>

<style lang="scss">
</style>
